(function() {
  'use strict';

  angular
    .module('moto', [
      'ngAnimate',
      'ngCookies',
      'ngTouch',
      'ngSanitize',
      'ngMessages',
      'ngAria',
      'ngResource',
      'ngRoute'
    ])

    .filter('encodeURI', encodeURI)                 // Filter for URL
    .filter('startsWithLetter', startsWithLetter)   // Filter for search input
    .filter('property', property)                   // Filter for category
    .filter('limitString', limitString);            // Filter for character limit

    function encodeURI() {
        return encodeURI;
    }

    function startsWithLetter() {

      return function (items, letter) {
        var filtered = [];
        var l = items.length;
        var letterMatch = new RegExp(letter, 'i');
        for (var i = 0; i < l; i++) {
          var item = items[i];
          if (letterMatch.test(item.title.substring(0, 32))) {
            filtered.push(item);
          }
        }
        return filtered;
      };

    }

    function property($filter) {

      var parseString = function(input){
        return input.split('.');
      };

      function getValue(element, propertyArray) {
        var value = element;

        angular.forEach(propertyArray, function(property) {
          value = value[property];
        });

        return value;
      }

      return function (array, propertyString, target) {
        var properties = parseString(propertyString);

        return $filter('filter')(array, function(item){
          return getValue(item, properties) === target;
        });
      };

    }

    function limitString() {

      return function (value, wordwise, max, tail) {
        if (!value) return '';

        max = parseInt(max, 10);
        if (!max) return value;
        if (value.length <= max) return value;

        value = value.substr(0, max);
        if (wordwise) {
          var lastspace = value.lastIndexOf(' ');
          if (lastspace != -1) {
            value = value.substr(0, lastspace);
          }
        }

        return value + (tail || ' â€¦');
        // usage ex:  | limitString:true:100:' ...'
      };
    }

})();

(function () {
  'use strict';

  angular
    .module('moto')
    .directive('motoNavbar', motoNavbar);

  /** @ngInject */

    function motoNavbar() {

      return {
        restrict: 'E',
        templateUrl: 'app/components/navbar/navbar.html',
        controller: 'NavbarController',
        controllerAs: 'vm',
        link: function($scope) {

          $scope.wrapper = document.getElementById('site-wrapper');
          $scope.navIcon = document.getElementById('cross');
          $scope.navTag = document.getElementsByClassName('linkName');

          function navFn() {
            if ($scope.wrapper.classList.contains('show-nav') === false) {
              $scope.wrapper.classList.add('show-nav');
              $scope.navIcon.classList.add('cross');
            } else {
              $scope.wrapper.classList.remove('show-nav');
              $scope.navIcon.classList.remove('cross');
            }
          }

          // bind on click function to Menu
          $scope.navIcon.addEventListener('click', navFn, false);

          // bind on click function to Menu List Items
          var navTagFunction = function() {

            var subMenu = this.nextElementSibling;

            if(subMenu !== null && subMenu.classList.contains('expandItem') === false) {
              subMenu.classList.add('expandItem');
            } else {
              subMenu.classList.remove('expandItem');
            }

          };

          for(var i=0; i<$scope.navTag.length; i++) {
            $scope.navTag[i].addEventListener('click', navTagFunction, false);
          }

          //// create a hammer swipe instance
          //$scope.vm = new Hammer($scope.wrapper);
          //
          //// listen to swipe events
          //$scope.vm.on('swiperight', function(e) {
          //  $scope.wrapper.classList.add('show-nav');
          //  $scope.navIcon.classList.add('cross');
          //  e.preventDefault();
          //});
          //$scope.vm.on('swipeleft', function(e) {
          //  $scope.wrapper.classList.remove('show-nav');
          //  $scope.navIcon.classList.remove('cross');
          //  e.preventDefault();
          //});

        }

      };

  }

}());

(function () {
 'use strict';

  angular
    .module('moto')
    .controller('NavbarController', NavbarController);

    /** @ngInject */

    function NavbarController($location) {

      var vm = this;

      vm.menuClass = function(page) {

        vm.current = $location.path().substring(1);
        return page === vm.current ? 'ms-menu__active' : '';

      };

    }

}());

(function() {
  'use strict';

  angular
    .module('moto')
    .factory('news', news);

      /** @ngInject */

      function news($http) {

        function getData(callback) {
            $http({
              method: 'GET',
              url: 'app/data/articles.json',
              cache: true
            }).success(callback);
        }

        return {
          list: getData,
          find: function (title, callback) {
              getData(function (data) {
                  var news = data.filter(function (entry) {
                      return entry.title === title;
                  })[0];
                  callback(news);
              });
          }
        };

      }

})();

(function() {
  'use strict';

  angular
    .module('moto')
    .controller('MainController', MainController);

  /** @ngInject */

  function MainController(news) {

    var vm = this;

    news.list(function(news) {
      vm.news = news;
    });

  }

})();

